.board__title{
  font-size: 1.2rem; 
  font-weight: 800;
}
.item{
  width: 100%;
  border: 2px solid royalblue;
  padding: 20px;
  border-radius: 10px;
  margin: 5px 0;
  cursor: grab;
  background-color: beige;
}
.back__button{
  background-color: #F7C955 ;
}
.board__section{
  padding: 20px;
}
.board__header{
  display: flex;
  align-items: center;
  margin-top: 70px;
  gap: 20px;
}
.board__title{
  font-size: 2rem;
}
.create__button{
  min-width: 130px;
  background-color: #99A33B;
}
.delete__button{
  min-width: 160px;
  background-color: #E53E3E;
}